:root *{
  margin: 0px;
  padding: 0px; 
}

#root{
  display: grid;
  justify-content: stretch;
}

h2, .h2 {
  font-size: 25px;
  font-weight:500;
}

button:hover {
  cursor: pointer;
}

.container {
  display: grid;
  grid-auto-flow: row;
  grid-auto-rows: 1fr auto;
  align-items: flex-start;
  min-height: 100vh;
}

.container.dark{
  background-color: #15202B;
  color:#F7F9F9;
}

.game {
  justify-self: center;
  display: grid;
  grid-auto-flow: row;
  text-align: center;
  padding: 10px 25px;
  /**/
  max-width: 1100px;
  width: 100%;
  box-sizing: border-box;
}

.game .error{
  font-size: 1.7rem;
  background-color: salmon;
  padding: 20px 0px;
}

.game header {
  margin-bottom: 20px;
}

.Board {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); */
  /* gap: 5px; */
  grid-template-columns: repeat(auto-fit, 140px);
  gap: 10px;
  /* grid-auto-flow: column; */
  justify-content: center;
}

.Footer {
  margin-top: auto;
  padding: 14px;
  background-color: hsl(0, 0%, 88%);
  font-size: 1rem;
  text-align: center;
}

.Footer.dark{
  background-color: black;
}


